ol, ul {
  margin: 0 0 1.5em 1.5em;
}

@font-face {
  font-family: "slick";
  src: url("./assets/fonts/slick.eot");
  src: url("./assets/fonts/slick.eot?#iefix") format("embedded-opentype"), url("./assets/fonts/slick.woff") format("woff"), url("./assets/fonts/slick.ttf") format("truetype"), url("./assets/fonts/slick.svg#slick") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@media (max-width: 781px) {
  .has-large-font-size {
    font-size: 18px !important;
  }
}
#masthead {
  height: 100vh;
  max-width: 250px;
  flex-wrap: wrap;
  position: fixed;
  left: 0;
  top: 0;
  padding-top: 100px;
  overflow: hidden;
  transition: max-width 0.3s ease-in-out;
}
#masthead #burger-menu svg {
  cursor: pointer;
}
#masthead .inside-header {
  flex-wrap: wrap;
  padding: 0 25px;
}
#masthead .inside-header #site-navigation {
  margin-left: 0;
}
#masthead .inside-header #site-navigation #primary-menu #menu-main-menu {
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: flex-start;
}
#masthead .inside-header #site-navigation #primary-menu #menu-main-menu a {
  padding: 0;
  display: flex;
  align-items: center;
}
#masthead .inside-header #site-navigation #primary-menu #menu-main-menu a span {
  opacity: 1;
  transition: opacity 0.2s ease-in-out, font-size 0.2s ease-in-out;
}
#masthead .inside-header #site-navigation #primary-menu #menu-main-menu a svg {
  margin-right: 20px;
  transition: margin-right 0.3s ease-in-out;
}
#masthead .inside-header #site-navigation #primary-menu #menu-main-menu a:hover svg path {
  fill: var(--accent);
}
#masthead .inside-header #site-navigation #primary-menu #menu-main-menu .current-menu-item a svg path {
  fill: var(--accent);
}
#masthead .inside-header .site-branding-container {
  flex-wrap: wrap;
}
#masthead .inside-header .site-branding-container .site-branding a {
  opacity: 1;
  transition: all 0.2s ease-in-out;
}
#masthead .inside-header .site-branding-container #burger-menu {
  width: 100%;
}

body.menu-closed #masthead {
  max-width: 90px;
}
body.menu-closed #masthead .inside-header #primary-menu #menu-main-menu a span {
  opacity: 0 !important;
  font-size: 0rem;
  transition: opacity 0.2s ease-in-out, font-size 0.3s ease-in-out 0.2s;
}
body.menu-closed #masthead .inside-header #primary-menu #menu-main-menu a svg {
  margin-right: 0px !important;
}
body.menu-closed #masthead .inside-header .site-branding-container .site-branding a {
  opacity: 0;
  font-size: 0;
}
body.menu-closed .top-bar {
  max-width: calc(100vw - 90px);
}

.top-bar {
  width: 100vw;
  max-width: calc(100vw - 250px);
  margin: 0 0 0 auto;
  transition: max-width 0.3s ease-in-out;
}
.top-bar .inside-top-bar {
  margin: 0;
}
.top-bar .inside-top-bar .widget:nth-child(2n+1) {
  order: unset;
}

.checklist-outer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.checklist-outer .title-and-count {
  display: grid;
  grid-template-columns: 70% 30%;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}
.checklist-outer .title-and-count p {
  transition: font-size 0.3s ease-in-out;
}
.checklist-outer p {
  width: 100%;
  text-align: center;
}

#account-checklist {
  display: flex;
  flex-direction: column;
  order: 4;
  overflow: hidden;
  max-height: 800px;
  transition: opacity 0.3s ease-in-out, max-height 0.3s ease-in-out, font-size 0.3s ease-in-out;
}
#account-checklist label {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
}
#account-checklist label input {
  pointer-events: none;
}

#checklist-percentage {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  width: 100%;
  height: 12px;
  max-width: 250px;
  border-radius: 2em;
  overflow: hidden;
  background-color: #EDF2F7;
}
#checklist-percentage span.active {
  background-color: var(--accent);
}
#checklist-percentage span.active.final {
  border-top-right-radius: 2em;
  border-bottom-right-radius: 2em;
}

.menu-closed .title-and-count {
  opacity: 0;
}
.menu-closed .title-and-count p {
  font-size: 0 !important;
}
.menu-closed #account-checklist {
  display: none;
}
.menu-closed #checklist-percentage {
  grid-template-columns: repeat(1, 1fr);
  max-width: 12px;
  height: 100px;
  border-radius: 2em;
}
.menu-closed #checklist-percentage .active:not(.final) {
  border-radius: 0;
}
.menu-closed #checklist-percentage .active.final {
  border-radius: 0 0 2em 2em;
}

body {
  display: flex;
  flex-wrap: wrap;
}
body.menu-closed #page, body.menu-closed .site-footer {
  max-width: calc(100vw - 90px);
}
body.menu-closed .site-footer {
  order: 4;
  width: 100vw;
}
body #page {
  order: 3;
  max-width: calc(100vw - 250px);
  margin: 0 0 0 auto;
  width: 100vw;
  transition: max-width 0.3s ease-in-out;
}
body .site-footer {
  order: 4;
  max-width: calc(100vw - 250px);
  width: 100vw;
  margin-left: auto;
  transition: max-width 0.3s ease-in-out;
}